import React from 'react'
import { Button } from 'reactstrap'
import i18n from 'i18n'

function Language () {
  return (
    <div>
      <Button color='primary' onClick={() => i18n.changeLanguage('pl')}>PL</Button>
      <Button color='primary' onClick={() => i18n.changeLanguage('en')}>EN</Button>
    </div>
  )
}

export default Language
