import React, { useState } from 'react'
import {
  Card,
  CardBody,
  Input,
  Button,
  Form
} from 'reactstrap'

export const MarketFilters = ({ handleSetOrderFilters }) => {
  const [locationPostCode, setLocationPostCode] = useState('')
  const handleClear = () => {
    setLocationPostCode('')
    handleSetOrderFilters({ locationPostCode: '' })
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    handleSetOrderFilters({ locationPostCode })
  }
  return (
    <Card>
      <CardBody>
        <Form onSubmit={handleSubmit}>
          <Input
            placeholder='Kod pocztowy' id='filter-locationPostCode' name='filter-locationPostCode' onChange={(e) => {
              setLocationPostCode(e.target.value)
            }} value={locationPostCode}
          />
          <Button onClick={handleClear}>Wyczyść</Button>
          <Button color='success'>Filtruj</Button>
        </Form>
      </CardBody>
    </Card>
  )
}

export default MarketFilters
