import React, { useState } from 'react'
import { Row, Col, Form, Input, Label, Button } from 'reactstrap'

const margin5px = { margin: '5px' }
const saveButtonStyle = { margin: '5px', float: 'right' }

function SubscriptionRow ({ subscription, handleUpdateSubscription }) {
  const [isActive, setIsActive] = useState(subscription.isActive)
  const [canRenew, setCanRenew] = useState(subscription.canRenew)
  const [nrOfOperats, setNrOfOperats] = useState(subscription.nrOfOperats)
  const [maxNrOfOperats, setMaxNumberOfOperats] = useState(subscription.maxNrOfOperats)
  const [nrOfGenerations, setNrOfGenerations] = useState(subscription.nrOfGenerations)
  const [maxNrOfGenerations, setMaxNrOfGenerations] = useState(subscription.maxNrOfGenerations)

  function submitUpdateSubscriptionForm () {
    const editedSubscription = {
      id: subscription.id,
      isActive,
      canRenew,
      nrOfOperats,
      maxNrOfOperats,
      nrOfGenerations,
      maxNrOfGenerations,
      createdDateTime: subscription.createdDateTime
    }
    handleUpdateSubscription(editedSubscription)
  }

  function updateSubscriptionForm () {
    return (
      <Form
        id='update-subscription-form' onSubmit={e => {
          e.preventDefault()
          submitUpdateSubscriptionForm()
        }}
      >
        <Row>
          <Col>
            <Label for='subscription-is-active'>
              <b>Aktywna:</b>
            </Label>
            <Input style={margin5px} id='subscription-is-active' type='checkbox' checked={isActive} onChange={_ => { setIsActive(!isActive) }} />
          </Col>
          <Col>
            <Label for='subscription-can-renew'>
              <b>Odnowi się w następnym miesiącu:</b>
            </Label>
            <Input style={margin5px} id='subscription-can-renew' type='checkbox' checked={canRenew} onChange={_ => { setCanRenew(!canRenew) }} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Label for='subscription-nr-of-operats'>
              <b>Liczba stworzonych operatów:</b>
            </Label>
            <Input style={margin5px} id='subscription-nr-of-operats' type='number' step='1' min='0' value={nrOfOperats} onChange={e => setNrOfOperats(parseInt(e.target.value))} />
          </Col>
          <Col>
            <Label for='subscription-max-nr-of-operats'>
              <b>Limit tworzenia operatów:</b>
            </Label>
            <Input style={margin5px} id='subscription-max-nr-of-operats' type='number' step='1' min='1' value={maxNrOfOperats} onChange={e => setMaxNumberOfOperats(parseInt(e.target.value))} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Label for='subscription-nr-of-generations'>
              <b>Liczba wygenerowanych pdf:</b>
            </Label>
            <Input style={margin5px} id='subscription-nr-of-generations' type='number' step='1' min='0' value={nrOfGenerations} onChange={e => setNrOfGenerations(parseInt(e.target.value))} />
          </Col>
          <Col>
            <Label for='subscription-max-nr-of-generations'>
              <b>Limit generowania pdf:</b>
            </Label>
            <Input style={margin5px} id='subscription-max-nr-of-generations' type='number' step='1' min='1' value={maxNrOfGenerations} onChange={e => setMaxNrOfGenerations(parseInt(e.target.value))} />
          </Col>
        </Row>
        <Row>
          <Col>
            <Button color='success' type='submit' style={saveButtonStyle}>Zapisz</Button>
          </Col>
        </Row>
      </Form>
    )
  }

  return (
    <div>
      {updateSubscriptionForm()}
    </div>
  )
}

export default SubscriptionRow
