import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { useLocation } from 'react-router-dom'
import {
  Button, Card,
  CardBody, Collapse, UncontrolledTooltip
} from 'reactstrap'
import { hideLoadingScreen } from 'views/Modal/actions'
import { userRolesConstants } from '../../constants'
import { operatService, operatTypeService, orderService } from '../../services'
import { clearOperatState, generateOperat, getFormatedFilesByOperat, loadOperatType } from './actions'
import CsvComponent from './CsvComponent'
import { InfoForAdmin } from './InfoForAdmin'
import InputArea from './InputArea'
import PhotoArea from './PhotoArea'
import saga from './saga'
import reducer from './reducer'
import { useInjectReducer } from 'utils/injectReducer'
import { useInjectSaga } from 'utils/injectSaga'
import Weights from './Weights'
function Create (props) {
  useInjectReducer({ key: 'operat', reducer })
  useInjectSaga({ key: 'operat', saga })

  const dispatch = useDispatch()
  const user = useSelector(state => state.global.user)
  const operatState = useSelector(state => state.operat)
  const [operat, setOperat] = useState({})
  const [order, setOrder] = useState({})
  const [operatTypeParams, setOperatTypeParams] = useState([])
  const { t } = useTranslation('common')

  const location = useLocation()
  useEffect(() => {
    const params = new URLSearchParams(location.search)
    operatService.get(params.get('id')).then(operatRes => {
      orderService.getSingleOrder(operatRes.orderId).then(orderRes => setOrder(orderRes))
      setOperat(operatRes)
      getFormatedFilesByOperat(dispatch, operatRes).then((operatTypeId) => {
        operatTypeService
          .getTypeParams(operatTypeId)
          .then((res) => {
            setOperatTypeParams(res)
            if (Array.isArray(res)) {
              res = { input: [], photo: [] }
              dispatch(loadOperatType(res))
            }
            dispatch(hideLoadingScreen())
          })
      })
    })

    return function cleanup () {
      dispatch(clearOperatState())
    }
  }, [dispatch, location.search])

  const reloadOperat = () => {
    return getFormatedFilesByOperat(dispatch, operat)
  }

  useState(false)
  const [inputArea, setInputArea] = useState(false)
  const [csvArea, setCsvArea] = useState(false)
  const [photoArea, setPhotoArea] = useState(false)
  const [weightsArea, setWeightsArea] = useState(false)
  const isAdmin = user.userType === userRolesConstants.ADMIN

  const isAllFilled = operatState.files.csv ? operatState.files.input.item1 && operatState.files.weights.item1 && operatState.files.csv.item1 && (operatState.files.photo.length === 10 || (operatState.photo.data && operatState.photo.data.every(x => x.data.every(t => t.hasOwnProperty('url') && !t.hasOwnProperty('file'))))) : false

  function createGenerateOperatInfo () {
    let result = ''
    if (!operatState.files.csv) {
      return ''
    }
    if (!operatState.files.input.item1) {
      result += 'Brak danych opisowych<br /> \n'
    }
    if (!operatState.files.csv.item1) {
      result += 'Brak cen transakcyjnych<br /> \n'
    }
    if (!operatState.photo.data || (operatState.files.photo.length !== 10 || (!operatState.photo.data && !operatState.photo.data.every(x => x.data.every(t => t.hasOwnProperty('url') && !t.hasOwnProperty('file')))))) {
      result += 'Brak zdjęć<br /> \n'
    }
    if (!operatState.files.weights.item1) {
      result += 'Brak wag cech<br /> \n'
    }
    return result
  }
  return (
    <div className='content'>
      {isAdmin && <><InfoForAdmin /><br /></>}

      <Button
        style={{ width: '16rem' }}
        color='success'
        onClick={() => dispatch(generateOperat())}
        disabled={!isAllFilled}
      >
        {t('create.buttons.generate')}
      </Button>
      {
        !isAllFilled && <>      <i style={{ padding: '14px' }} className='fa fa-solid fa-question' id='cannot-generate-info' />
          <UncontrolledTooltip
            placement='top'
            target='cannot-generate-info'
          >
            <div dangerouslySetInnerHTML={{ __html: createGenerateOperatInfo() }} />
          </UncontrolledTooltip>
        </>
      }

      <br />
      <div>
        <Button
          color='info'
          onClick={() => setInputArea(!inputArea)}
          disabled={!operatTypeParams.input}
          style={{ marginBottom: '1rem', width: '16rem' }}
        >
          {t('create.buttons.description')}
        </Button>
        <Collapse isOpen={inputArea}>
          <Card>
            <CardBody>
              {inputArea && <InputArea reloadOperat={reloadOperat} operatTypeParams={operatTypeParams} operat={operat} order={order} />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
      <div>
        <Button
          disabled={operatTypeParams.length === 0}
          color='info'
          onClick={() => setCsvArea(!csvArea)}
          style={{ marginBottom: '1rem', width: '16rem' }}
        >
          {t('create.buttons.csvFiles')}
        </Button>
        <Collapse isOpen={csvArea}>{csvArea && <CsvComponent reloadOperat={reloadOperat} operatTypeParams={operatTypeParams} operat={operat} />}</Collapse>
      </div>
      <div>
        <Button
          disabled={operatTypeParams.length === 0}
          color='info'
          onClick={() => setWeightsArea(prev => !prev)}
          style={{ marginBottom: '1rem', width: '16rem' }}
        >
          Wagi cech
        </Button>
        <Collapse isOpen={weightsArea}>
          {weightsArea && <Weights operatTypeParams={operatTypeParams} operat={operat} order={order} />}
        </Collapse>
      </div>
      <div>
        <Button
          disabled={operatTypeParams.length === 0}
          color='info'
          onClick={() => setPhotoArea(!photoArea)}
          style={{ marginBottom: '1rem', width: '16rem' }}
        >
          {t('create.buttons.photos')}
        </Button>
        <Collapse isOpen={photoArea}>
          {photoArea && <PhotoArea operatTypeParams={operatTypeParams} operat={operat} order={order} />}
        </Collapse>
      </div>
    </div>
  )
}

export default Create
