import React, { useState } from 'react'
import Creatable from 'react-select/creatable'
import { useTranslation } from 'react-i18next'
import { Button, Form, FormGroup, Input, Label, UncontrolledTooltip, UncontrolledPopover, PopoverBody, Col, UncontrolledAlert, FormFeedback } from 'reactstrap'
import { MapWithMarker } from '../../views/Maps'
import InputMask from 'react-input-mask'
import LoadingIndicator from 'components/LoadingIndicator'
import { validateLandRegisterCode } from 'utils/validateLandRegisterCode'
export const NewOrderForm = (props) => {
  const { t } = useTranslation('common')
  const [name, setName] = useState('')
  const [description, setDescription] = useState('')
  const [address, setAddress] = useState('')
  const [authorizedExperts, setAuthorizedExperts] = useState(false)
  const [latlng, setLatlng] = useState({ lat: 0, lng: 0 })
  const [locationPostCode, setLocationPostCode] = useState('')
  const [shouldBeValidated, setShouldBeValidated] = useState(false)
  const [orderSubject, setOrderSubject] = useState(null)
  const [valuationScope, setValuationScope] = useState(null)
  const [landRegisterCode, setLandRegisterCode] = useState('')
  const [propertyIdentification, setPropertyIdentification] = useState('')
  const [isEkwInvalid, setIsEkwInvalid] = useState(false)
  const [wrongEkwMessage, setWrongEkwMessage] = useState('')
  const addOrder = (e) => {
    e.preventDefault()
    if (landRegisterCode !== '') {
      const validationResult = validateLandRegisterCode(landRegisterCode, props.ekwData)
      if (validationResult !== '') {
        setIsEkwInvalid(true)
        setWrongEkwMessage(validationResult)
        return
      }
    }
    props.onSubmit()
    setIsEkwInvalid(false)
    setWrongEkwMessage('')
  }
  const subjectOptions = [
    { value: 'Lokal mieszkalny', label: 'Lokal mieszkalny' },
    { value: 'Działka rolna', label: 'Działka rolna' }
  ]

  const valuationScopeOptions = [
    { value: 'Na potrzeby sprzedaży', label: 'Na potrzeby sprzedaży' },
    { value: 'Prawo użytkowania wieczystego do gruntu', label: 'Prawo użytkowania wieczystego do gruntu' }
  ]
  function checkAllRequiresFields () {
    return name === '' || locationPostCode === '' || orderSubject === '' || valuationScope === ''
  }

  return (
    <div>
      <Form
        onSubmit={(e) => addOrder(e)}
        id='new-order-form'
      >
        <input type='hidden' value={`${latlng.lat},${latlng.lng}`} name='latlng-value' />
        <FormGroup>
          <Label for='orderName'>{t('market.table.headers.name')}</Label>
          <Input
            type='text'
            name='orderName'
            id='orderName'
            value={name}
            onChange={(e) => {
              setName(e.target.value)
            }}
            required
          />
          <UncontrolledTooltip
            placement='top'
            target='orderName'
          >
            {t('market.newOrder.namePlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Label for='description'>{t('market.table.headers.description')}</Label>
          <Input
            type='textarea'
            name='description'
            id='description'
            value={description}
            onChange={(e) => {
              setDescription(e.target.value)
            }}
          />
          <UncontrolledTooltip
            placement='top'
            target='description'
          >
            {t('market.newOrder.descriptionPlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Label for='postal-code'>{t('market.newOrder.postalCode')}</Label>
          <InputMask
            mask='99-999'
            value={locationPostCode}
            onChange={(e) => {
              setLocationPostCode(e.target.value)
            }}
          >
            {(inputProps) => <Input
              {...inputProps}
              type='text'
              name='postal-code'
              id='new-order-postal-code'
              pattern='\d{2}-\d{3}$'
              required
                             />}
          </InputMask>
        </FormGroup>
        <FormGroup>
          <Label for='address'>{t('common.userInfo.address')}</Label>
          <Input
            type='text'
            name='address'
            id='address'
            value={address}
            onChange={(e) => {
              setAddress(e.target.value)
            }}
          />
          <UncontrolledTooltip
            placement='top'
            target='address'
          >
            {t('market.newOrder.addressPlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Button
            className='long-btn' id='UncontrolledPopoverMAP'
            type='button' color='success'
          >Dodaj lokalizację na mapie
          </Button>
          <UncontrolledPopover
            placement='right'
            target='UncontrolledPopoverMAP'
            trigger='legacy'
          >
            <PopoverBody>
              <MapWithMarker
                locationInfo={{ address, locationPostCode, latlng }}
                setCoords={(cords) => { setLatlng(cords) }}
              />
            </PopoverBody>
          </UncontrolledPopover>
        </FormGroup>
        <FormGroup>
          <Label for='order-subject'>Co chcesz wycenić?</Label>
          <Creatable options={subjectOptions} onChange={e => { setOrderSubject(e.value) }} />
          <input
            tabIndex={-1}
            autoComplete='off'
            value={orderSubject} id='order-subject' name='order-subject' style={{ opacity: 0, height: 0 }} required
          />
        </FormGroup>
        <FormGroup>
          <Label for='quotation-range'>Zakres wyceny</Label>
          <Creatable options={valuationScopeOptions} onChange={e => { setValuationScope(e.value) }} />
          <input
            tabIndex={-1}
            autoComplete='off'
            value={valuationScope} id='valuation-scope' name='valuation-scope' style={{ opacity: 0, height: 0 }} required
          />
        </FormGroup>
        <FormGroup>
          <Label for='order-land-register-code'>Nr księgi wieczystej (pole bedzie tylko widoczne dla rzeczoznawcy wykonującego)</Label>
          <InputMask
            mask='aa9a/99999999/9'
            value={landRegisterCode}
            onChange={(e) => {
              setLandRegisterCode(e.target.value)
            }}
            invalid={isEkwInvalid}

          >
            {(inputProps) => <Input
              {...inputProps} type='text'
              style={{ textTransform: 'uppercase' }}
              name='order-land-register-code'
              id='order-land-register-code'
                             />}
          </InputMask>
          <FormFeedback>
            <div
              dangerouslySetInnerHTML={{ __html: wrongEkwMessage }}
            />
          </FormFeedback>
        </FormGroup>
        <FormGroup>
          <Label for='property-identification'>Identyfikacja nieruchomości np. numer nieruchomości. Pole to będzie widoczne tylko dla rzeczoznawcy wykonującego</Label>
          <Input
            id='property-identification'
            name='property-identification'
            type='text'
            value={propertyIdentification}
            onChange={e => setPropertyIdentification(e.target.value)}
          />
        </FormGroup>
        <Button
          color='success'
          disabled={checkAllRequiresFields()}
        >
          {t('common.add')}
        </Button>
        <FormGroup>
          <Col sm={{ size: 10 }}>
            <FormGroup>
              <Label for='authorizedExperts' sm={10}>
                <Input
                  type='checkbox'
                  name='authorizedExperts'
                  id='authorizedExperts'
                  value={authorizedExperts}
                  onChange={(e) => {
                    setAuthorizedExperts(e.target.checked)
                  }}
                />
                {t('market.table.headers.authorizedExperts')}
              </Label>
            </FormGroup>
          </Col>
        </FormGroup>
        <FormGroup>
          <Col sm={{ size: 10 }}>
            <FormGroup>
              <Label for='aoperatValidation' sm={10}>
                <Input
                  type='checkbox'
                  name='aoperatValidation'
                  id='aoperatValidation'
                  checked={shouldBeValidated}
                  onChange={(e) => {
                    setShouldBeValidated(e.target.checked)
                  }}
                />
                {t('market.table.headers.aoperatValidation')}
              </Label>
            </FormGroup>
          </Col>
        </FormGroup>
      </Form>
      {props.saveOrder.error &&
        <UncontrolledAlert
          color='danger'
        >
          <div
            dangerouslySetInnerHTML={{ __html: props.saveOrder.error.message }}
          />
        </UncontrolledAlert>}
      {props.saveOrder.loading && <LoadingIndicator />}
    </div>
  )
}

export default NewOrderForm
